<template>
  <div class="film-box" style="height:100%">
    <!-- <div class="content" style="height:100%"> -->
    <div class="content" :style="{ minHeight: curHeight+'px'}" >
      <div class="top">
        <img src="../../assets/image/login/back.png" alt="" class="back" @click="isback()">
      </div>
      <div class="middle-box">
        <div class="main">
          <div class="logo">
            <img src="../../assets/image/logo.png" alt="">
          </div>
          <div class="form">
            <p class="logo-text" >登录</p>
              <div class="username-box">
                <img src="../../assets/image/login/user.png" alt="" style="margin-left:0">
                <input type="text" class="username" placeholder="手机号 / 邮箱" v-model="parameter.tel">
                <!-- <img src="../../assets/image/login/close.png" alt="" class="right-icon" style="margin-right:0"> -->
              </div>
              <div class="username-box">
                <img src="../../assets/image/login/password.png" alt="" style="margin-left:0">
                <input type="password" class="username" placeholder="请输入密码" v-model="parameter.pass">
                <!-- <img src="../../assets/image/login/eye_close.png" alt="" class="right-icon" style="margin-right:0"> -->
              </div>
          </div>
          <!--            登录按钮-->
          <div class="logo-btn" @click="userLogin()">
            登录
          </div>
<!--          7天免登陆 忘记密码-->
          <div class="bottom-box">
            <div class="bottom-left">
<!--              <span class="rectangle"></span>-->
              <van-checkbox v-model="checked" shape="square">7天免登陆</van-checkbox>
            </div>
            <div class="bottom-right" @click="toforgetpassword()">忘记密码</div>
          </div>
        </div>
        <div class="footer">
          还没有账号？ <span style="color: #079FDC" @click="toRegister()">立即注册</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { login, loginfree } from '@/api/user'
// import Cryptojs from 'crypto-js'
// import { objKeySort, sgin, isLoginfree } from '@/utils/auth'
import { objKeySort, sgin } from '@/utils/auth'
import { Checkbox, CheckboxGroup, Toast } from 'vant'
Vue.use(Checkbox).use(CheckboxGroup).use(Toast)
export default {
  components: {},
  data () {
    return {
      checked: true,
      pass: '', // 手机号码
      tel: '', // 密码
      curHeight: '',
      parameter: {
        pass: '',
        tel: ''
      },
      datas: {
        tel: ''
      }
    }
  },
  created () {
    this.beforeMount()
  },
  methods: {
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    isback () {
      this.$router.go(-1)
    },
    userLogin () {
      var that = this
      if (that.parameter.tel === '') {
        that.$toast({ message: '手机号不能为空', duration: 1500 })
        return
      }
      if (!(/^[0-9]{11}$/.test(that.parameter.tel))) {
        that.$toast({ message: '请输入正确手机号', duration: 1500 })
        return
      }
      if (that.parameter.pass === '') {
        that.$toast({ message: '密码不能为空', duration: 1500 })
        return
      }
      var data = objKeySort(this.parameter)
      var token = sgin(data)
      this.parameter.token = token
      that.transactionList(this.parameter)
    },
    toRegister () {
      this.$router.push({
        path: '/register',
        query: {
        }
      })
    },
    toforgetpassword () {
      console.log(1111)
      this.$router.push({
        path: '/forgetpassword',
        query: {
        }
      })
    },
    transactionList (parameter) {
      var that = this
      login(parameter).then(res => {
        if (res.code === 0) {
          console.log(res, res.data.u_id)
          that.$toast({ message: '登录成功', duration: 1200 })
          localStorage.setItem('userTel', that.parameter.tel)
          sessionStorage.setItem('u_id', res.data.u_id)
          sessionStorage.setItem('usdt_available_balance', res.data.usdt_available_balance)
          sessionStorage.setItem('transaction_password', res.data.transaction_password)
          if (that.checked) {
            that.sevenDays()
          } else {
            // that.sevenDays()
          }
          that.parameter.tel = ''
          that.parameter.pass = ''
          delete that.parameter.token
          setTimeout(() => {
            // that.$router.go(-1)
            this.$router.push({ path: '/', query: {} })
          }, 1500)
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.parameter.tel = ''
        that.parameter.pass = ''
        delete that.parameter.token
      })
    },
    sevenDays () {
      var that = this
      that.datas.tel = that.parameter.tel
      var data = objKeySort(that.datas)
      var token = sgin(data)
      console.log(token, data)
      that.datas.token = token
      console.log(that.datas, 'this.datas')
      loginfree(that.datas).then(res => {
        console.log(res)
      }).catch((err) => console.log(err))
    }
  }
}
</script>
<style scoped>
  .content{
    background: #12172B;
  }
  .bottom-left >>> .van-checkbox__label{
    color: #A4A5A8;
  }
  .bottom-left >>> .van-checkbox__icon{
    background-color: #12172B;
  }
  .film-box {
    position: relative;
  }

  .viewRecord {
    position: absolute;
    right: 2rem;
    top: 2rem;
    z-index: 20;
    background: #fff;
    border-radius: 100%;
  }

  .collection {
    position: absolute;
    right: 2.5rem;
    top: 2rem;
    z-index: 20;
    background: #fff;
    border-radius: 100%;
  }
  .top{
    height: 5rem;
    background: url("../../assets/image/login/bg1.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  .back{
    width: 0.44rem;
    height: 0.44rem;
    position: fixed;
    left: 0.62rem;
    top: 0.6rem;
  }
  .middle-box{
    background-color: #12172B;
    /* background: #000; */
  }
  .main{
    width:6.6rem;
    height:8.36rem;
    background:rgba(27,33,59,1);
    box-shadow:0rem -0.1rem 0.2rem 0rem rgba(11,17,35,0.2);
    border-radius:0.3rem;
    margin: 0 auto;
    text-align: left;
    position: relative;
    top: -1.5rem;
    padding-bottom: 0.2rem;
  }
  .logo{
    width: 2rem;
    height: 2rem;
    margin: 0 auto;
    background:linear-gradient(0deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius:0.4rem;
    position: relative;
    top: -1rem;
  }
  .logo img{
    width: 100%;
    height: 100%;
  }
  .logo-text{
    margin-left: 0.48rem;
    /* color: white; */
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.36rem;
  }
  .form img{
    width: 0.4rem;
    height: 0.4rem;
    vertical-align: middle;
    margin-left: 0.43rem;
    margin-right: 0.3rem;
  }
  .right-icon{
    float: right;
    margin-right: 0.49rem;
  }
  .username-box{
    margin-top: 0.5rem;
    font-size: 0.28rem;
    color: #595C68;
    display: flex;
    padding-bottom:0.33rem;
    margin:  0.5rem 0.43rem 0 0.43rem;
    border-bottom: 0.02rem solid #13192E;
  }
  .username{
    background: none;
    border: none;
    flex: 1;
  }
  .logo-btn{
    width:5.6rem;
    height:0.8rem;
    line-height: 0.8rem;
    text-align: center;
    background:linear-gradient(0deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius:0.1rem;
    /* color: white; */
    color: rgba(255, 255, 255, 0.7);
    margin: 0 auto;
    margin-top: 0.764rem;
  }
  .bottom-box{
    width: 5.6rem;
    display: flex;
    justify-content: space-between;
    /*background-color: orange;*/
    margin: 0 auto;
    margin-top: 0.446rem;
    /*padding: 0 0.3rem;*/
    color: #A4A5A8;
    font-size: 0.2362rem;
    height: 0.5rem;
    line-height: 0.5rem;
    font-weight:400;
    padding-bottom: 2.5rem;
  }
  .rectangle{
    display: inline-block;
    width: 0.2rem;
    height: 0.2rem;
    background-color: #12172B;
    margin-right: 0.2rem;
    vertical-align: middle;
    margin-bottom: 0.06rem;
    /*position: relative;*/
    /*top: 0.25rem;*/
    /*transform: translate(0,-50%);*/
  }
  .footer{
    font-size: 0.24rem;
    z-index: 999999;
    position: relative;
    top: -1rem;
    text-align: center;
    color: #A4A5A7;
  }
</style>
